export default function Card({ items }) {
  let badgeText;

  if (items.openSpots === 0) {
    badgeText = "SOLD OUT";
  } else if (items.location === "Online") {
    badgeText = "ONLINE";
  }

  return (
    <div className="card">
      {badgeText && <div className="card--badge">{badgeText}</div>}
      <img src={items.coverImg} className="card--image" />
      <div className="card--stats">
        <img
          src="https://i.postimg.cc/J0gDQr8q/Star-1.png"
          className="card--star"
        />
        <span>{items.stats.rating}</span>
        <span className="gray">({items.stats.reviewCount}) • </span>
        <span className="gray">{items.location}</span>
      </div>
      <p className="card--title">{items.title}</p>
      <p className="card--price">
        <span className="bold">From ${items.price}</span> / person
      </p>
    </div>
  );
}

